* {
  box-sizing: border-box;
}
.title {
  color: rgba(255, 255, 255, 0.6);
  font-size: 30px;
  text-align: center;
  padding-bottom: 30px;
}
.transparent {
  position: relative;
  max-width: 400px;
  max-height: 250px;
  padding: 30px 50px;
  margin: 50px auto 0;
  margin-top: 0px;
  margin-bottom: 0px;
}
.transparent:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.formInner {
  position: relative;
}
.formInner h3 {
  position: relative;
  margin-top: 0;
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 26px;
  text-transform: uppercase;
}
.formInner h3:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -6px;
  height: 2px;
  width: 60px;
  background: #1762ee;
}
.formInner label {
  display: block;
  text-align-last: center;
  font-family: "Roboto", sans-serif;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  font-size: 14px;
}
.formInner input {
  display: block;
  text-align-last: auto;
  font-size: 18px;
  width: 100%;
  padding: 0 15px;
  margin: 10px 0 15px;
  border-width: 0;
  line-height: 40px;
  border-radius: 20px;
  color: white;
  background: rgba(255, 255, 255, 0.2);
  font-family: "Roboto", sans-serif;
}
.formInner input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}

.formInner input[type="submit"] {
  background: #1762ee;
}

.button {
  display: block;
  margin: auto;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.6);
  border: none;
  padding: 15px;
  border-radius: 20px;
}

.button:hover,
.button:focus {
  color: white;
  background-color: #4356dc;
}
